* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #f3f3f3;

}

.center {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 800px;
  height: 100vh;
  overflow: hidden;
  margin: 0 auto;
  padding: 15px;
}

.board {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 300px;
  background-color: #313131;
  padding: 15px;
}

.board .card {
  padding: 15px;
  background-color: #f3f3f3;
  cursor: pointer;
  margin-bottom: 15px;
}